/* STATIC STYLES (desktop-first) */

.timeline-container {
  position: relative;

  margin-top: 2rem;
  padding: 2rem 0;
}

.timeline-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;

  width: 4px;
  height: 100%;

  background: var(--color-primary-dark);

  transform: translateX(-50%);
}

.timeline-line {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 2;

  width: 4px;
  height: 100%;

  background: linear-gradient(to bottom, var(--color-primary-text), var(--color-secondary-dark));

  transform: translateX(-50%) scaleY(0);
  transform-origin: top;
}

.timeline-item {
  position: relative;
  z-index: 3;

  display: flex;
  justify-content: center;

  margin-bottom: 3rem;

  gap: 3rem;
}

.timeline-node {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 3.5rem;
  height: 3.5rem;

  border: 1px solid var(--color-primary-dark);
  border-radius: 50%;

  background: var(--color-primary-darker);

  color: var(--color-secondary);
}

.timeline-content {
  width: 20rem;
}

.timeline-description {
  width: 20rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .timeline-content, .timeline-description {
    width: 15rem;
  }
}

@media (max-width: 768px) {
  .timeline-container::before, .timeline-line {
    left: 3rem;
  }

  .timeline-item {
    position: relative;

    display: grid;
    align-items: center;
    grid-template-rows: auto auto;
    grid-template-columns: 6rem 1fr;
    grid-template-areas: 'node description'
    'node content';
    justify-items: center;

    margin-bottom: 2rem;
    padding-left: 0;

    gap: 0;
  }

  .timeline-node {
    grid-area: node;
  }

  .timeline-description {
    grid-area: description;

    margin-bottom: 1rem;

    text-align: center;
  }

  .timeline-content {
    grid-area: content;

    width: 70%;
  }
}

@media (max-width: 576px) {
  .timeline-container::before, .timeline-line {
    left: 1rem;
  }

  .timeline-item {
    display: grid;
    grid-template-columns: 1rem 1fr;
  }

  .timeline-node {
    margin-left: 1rem;
  }

  .timeline-content, .timeline-description {
    width: 80%;
    margin-right: -2rem;
  }
}
