/* STATIC STYLES (desktop-first) */

.card4 {
  position: relative;
  z-index: 40;

  overflow: hidden;
  border: 1px solid var(--color-primary-darker);
  border-radius: 10rem;

  background-color: var(--color-primary-darkest);

  transition: transform 0.3s ease, box-shadow 0.3s ease;

  aspect-ratio: 5/6;
}

.card4:hover {
  transform: translateY(-5px);
}

.card4::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;

  width: 100%;
  height: 100%;

  border: 2px solid transparent;
  border-radius: 10rem;

  background: radial-gradient(circle, var(--color-primary-dark) 0%, var(--color-primary-darker) 100%);
  background-attachment: fixed;
  opacity: 0;

  transform: translate(-50%, -50%);
  transition: opacity 0.5s ease;
}

.card4:hover::before {
  opacity: 1;
}

.card4-background {
  position: absolute;
  bottom: -100%;
  left: 0;

  width: 100%;
  height: 100%;

  background-color: var(--color-primary-dark);

  transition: bottom 0.7s ease;
}

.card4:hover .card4-background {
  bottom: 0;
}

