/* STATIC STYLES (desktop-first) */

.experience-card {
  display: flex;
  justify-content: center;

  padding: 2rem 3rem;
}

.experience-card-inner p {
  display: flex;
  align-items: center;

  margin-bottom: 0.5rem;

  gap: 0.5rem;
}

.experience-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  padding: 5rem 0;

  gap: 2rem;;
}

.experience-icons > * {
  flex: 0 0 16rem;
}

.icon-card-content {
  position: relative;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;
  padding: 1.5rem;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
  .icon-card-content {
    padding: 1.2rem;
  }
}

@media (max-width: 768px) {
  .experience-icons > * {
    flex: 0 0 10rem;
  }
}

@media (max-width: 576px) {
  .icon-card-content {
    padding: 1rem;
  }
}
